import DeleteIcon from "@mui/icons-material/DeleteOutlined";
import EditIcon from "@mui/icons-material/EditOutlined";
import InfoOutlined from "@mui/icons-material/InfoOutlined";
import MoreVertOutlined from "@mui/icons-material/MoreVertOutlined";
import { Box, Card, Chip, IconButton, Skeleton, Stack, Typography } from "@mui/material";
import flatten from "lodash/flatten";
import sortBy from "lodash/sortBy";
import { useSnackbar } from "notistack";
import { Outlet, useLocation, useNavigate, useParams } from "react-router-dom";
import { useMiddleware, useTask } from "../../../../Application";
import { useAuth } from "../../../../auth";
import ResponsiveActionBar from "../../../../components/ResponsiveActionBar";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import ResponsiveTabBar from "../../../../components/ResponsiveTabBar";
import { TabView } from "../../../../tabs";
import useDialogProps from "../../../../utils/useDialogProps";
import useFetch from "../../../../utils/useFetch";
import useNavigateTask from "../../../../utils/useNavigateTask";
import PersonDeleteDialog from "../../components/PersonDeleteDialog";
import PersonFormDialog from "../../components/PersonFormDialog";


const PersonDetails = () => {

    const { inject } = useAuth();

    const middleware = useMiddleware('People.Main');

    const location = useLocation();
    const navigate = useNavigate();
    const params = useParams();
    const navigateTask = useNavigateTask();

    const { data, loading, reload } = useFetch({
        deps: [params.personId],
        handler: () => middleware.details(params.personId),
        initialState: {
            data: { person: {} },
            loading: true
        }
    });

    const { person } = data;

    const { enqueueSnackbar } = useSnackbar();

    const [formDialogProps, openFormDialog, closeFormDialog] = useDialogProps("edit", {
        hashDeps: ["defaultValues"]
    })
    const handleUpdate = () => {
        enqueueSnackbar('مشخصات شخص بروزرسانی گردید.');
        closeFormDialog();
        reload();
    }

    const taskManage = useTask('Products.manage');
    const [deleteDialogProps, openDeleteDialog, closeDeleteDialog] = useDialogProps("delete", {
        hashDeps: ["person"]
    })
    const handleDelete = () => {
        enqueueSnackbar('شخص حذف گردید.');
        closeDeleteDialog();
        navigateTask(taskManage, { replace: true });
    }

    const injectedHooks = Object.assign({}, ...(inject('People', 'PersonDetails.Hooks') || []));

    const task = useTask('People.details')


    return (
        <TabView
            loading={loading}
            {...Object.assign(
                {},
                task(person),
                ...(inject('People', 'PersonDetails.TabView', { loading, person }) || [])
            )}
        >
            <ResponsiveContainer fullHeight>
                <Card
                    variant="outlined"
                    sx={{
                        flexGrow: 1,
                        display: 'flex',
                        flexDirection: 'column'
                    }}
                >
                    <Box sx={{
                        display: "flex",
                        alignItems: 'start',
                        justifyContent: 'start',
                        p: { xs: 2, sm: 3 }
                    }}
                    >
                        <Box sx={{ flexGrow: 1, display: 'flex', alignItems: 'center' }}>
                            {inject('People', 'PersonDetails.PreHeader', { person, loading, reload, params, ...injectedHooks })}

                            <Box sx={{ flexGrow: 1 }}>
                                <Typography variant="h5" sx={{ fontWeight: 600, mb: 1 }}>
                                    {loading ? <Skeleton variant="text" width="25%" /> : person.fullName}
                                </Typography>
                                {loading ? <Skeleton variant="text" width="50%" /> : (
                                    <Stack direction="row" spacing={0.5} sx={{ display: "inline-flex" }}>
                                        {data.person?.roles && person.roles.map(
                                            (role, index) => <Chip key={index} label={role.name} size="small" />)
                                        }
                                    </Stack>
                                )}
                            </Box>
                        </Box>
                        {!loading && (
                            <ResponsiveActionBar
                                actions={sortBy([
                                    {
                                        title: 'ویرایش',
                                        icon: <EditIcon fontSize="inherit" />,
                                        onClick: () => openFormDialog({ defaultValues: person }),
                                        buttonProps: {
                                            variant: "contained"
                                        },
                                        priority: 100
                                    },
                                    {
                                        title: 'حذف',
                                        icon: <DeleteIcon fontSize="inherit" />,
                                        onClick: () => openDeleteDialog({ person }),
                                        buttonProps: {
                                            variant: "contained"
                                        },
                                        priority: 200
                                    },
                                    ...flatten((inject('People', 'PersonDetails.ActionsList', { person, loading, reload, params, ...injectedHooks }) || []))
                                ], o => o.priority)}
                                ResponsiveActionComponent={IconButton}
                                ResponsiveActionIcon={<MoreVertOutlined />}
                            />
                        )}
                    </Box>
                    <ResponsiveTabBar
                        tabs={sortBy([
                            {
                                label: 'مشخصات شخص',
                                value: `/person/${params.personId}`,
                                icon: <InfoOutlined />,
                                priority: 100
                            },
                            ...(inject('People', 'PersonDetails.TabsList', { person, loading, reload, params, ...injectedHooks }) || [])
                        ].map(o => ({
                            ...o, onClick: () => navigate(
                                o.value,
                                { state: { prev: location } }
                            )
                        })), o => o.priority)}
                        value={location.pathname}
                    >
                        <Outlet context={{ ...data, loading }} />
                    </ResponsiveTabBar>
                </Card>

                <PersonFormDialog
                    {...formDialogProps}
                    onCreateOrUpdate={handleUpdate}
                />

                <PersonDeleteDialog
                    {...deleteDialogProps}
                    onDelete={handleDelete}
                />

                {inject('People', 'PersonDetails.Footer', { person, loading, reload, params, ...injectedHooks })}
            </ResponsiveContainer>
        </TabView>
    )
}

export default PersonDetails